@import url(https://fonts.googleapis.com/icon?family=Material+Icons);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.list {
    width: 90%;
    margin: auto;
    padding-left: 0;
    padding-top: 80px;
}

li {
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    align-items: center;
    margin: 10px 0;
    background-color: whitesmoke;
}

.img {
    width: 15%;
}

.name {
    font-weight: 500;
    text-transform: uppercase;
    font-size: 20px;
}

.color {
    color: darkslategray;
}

.price {
    color: red;
    font-weight: 400;
}

.addButton {
    border-radius: 20px;
    border: 1px solid red;
    color: red;
    font-weight: 600;
    padding: 7px 12px;
}

.icon {
    float: right;
    margin: 25px 70px;
}

.iconButton {
    border: none;
    background: white;
    cursor: pointer;
}

.iconButton:hover,
.iconButton:focus {
    outline: none;
}

.cards {
    width: 90%;
    margin: auto;
    padding-left: 0;
    display: flex;
    flex-flow: wrap;
    justify-content: space-between;
    text-align: center;
}

.card {
    border: 1px solid darkgray;
    width: 30%;
    margin: 10px 5px;
    background-color: whitesmoke;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.cardImg {
    width: 210px;
}

.cardDesc {
    display: flex;
    margin: 10px 0;
    align-items: center;
}

.cardDesc .price {
    margin-right: 50px;
}
